.product {
  display: flex;
  flex-direction: column;
  width: 200px;
  margin-top: 50px;
  padding: 32px 16px 16px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  
  &__photo {
    width: 160px;
    height: 134px;
    margin: 0 auto 40px;
  }
    
  &__description {
    padding-bottom: 4px;
    font-style: normal;
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
    color: $main-gray-color;
  }
    
  &__code {
    padding-bottom: 16px;
    font-style: normal;
    font-size: 10px;
    font-weight: 400;
    line-height: 14px;
    color: #616070;
  }
    
    
  &__genural-price {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    margin: 23px 0 16px;
  }
    
  &__price {
    margin: 0;
    font-size: 12px;
    font-weight: 400;
    line-height: 18px;
    color: #616070;
  }
    
  &__number-price {
    margin: 0;
    font-style: normal;
    font-size: 16px;
    line-height: 18px;
    font-weight: 700;
    color: $main-gray-color;
  }
    
  .buy {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40px;
    font-style: normal;
    font-size: 14px;
    line-height: 16px;
    font-weight: 700;
    color: $main-white-color;
    background-color: $main-blue-color;
    border-radius: 5px;
    text-transform: uppercase;
    text-decoration: none;
  }
    
  .buy:hover {
    color: $main-blue-color;
    background-color: $main-white-color;
    border: 1px solid $main-blue-color;
  }
  
}
